import {Component, ChangeDetectionStrategy, Input, Output, EventEmitter, HostBinding, TemplateRef} from '@angular/core';


export class NglInternalDatatableHeadCell {

  
   heading: string;
   headingTpl: TemplateRef<any>;

  get header() {
    return this.headingTpl || this.heading;
  }

  
   sortable: boolean;

   sortOrder: 'asc' | 'desc';

  
  get ariaSort() {
    return this.sortOrder ? `${this.sortOrder}ending` : null;
  }

   onSort = new EventEmitter();

  sortChange() {
    this.onSort.emit(this.sortOrder === 'desc' ? 'asc' : 'desc');
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'th[ngl-internal-datatatable-head]',
  template: `
    <div *ngIf="!sortable" [attr.title]="heading" [nglInternalOutlet]="header" class="slds-truncate"></div><a *ngIf="sortable" (click)="sortChange()" class="slds-th__action slds-text-link--reset"> <span class="slds-assistive-text">Sort Column</span><span [attr.title]="heading" [nglInternalOutlet]="header" class="slds-truncate"></span><div title="Sort Column" class="slds-icon_container"><svg nglIcon="arrowdown" class="slds-icon slds-icon--x-small slds-icon-text-default slds-is-sortable__icon"></svg></div></a>
  `,
  changeDetection: ChangeDetectionStrategy.OnPush,
  host: {
    'scope': 'col',
    '[class.slds-is-sorted--asc]': `sortOrder === 'asc'`,
    '[class.slds-is-sorted--desc]': `sortOrder === 'desc'`,
    '[class.slds-is-sorted]': `!!sortOrder`,
  },
}, ] },
];
/** @nocollapse */
static ctorParameters: ({type: any, decorators?: DecoratorInvocation[]}|null)[] = [
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'heading': [{ type: HostBinding, args: ['attr.title', ] },{ type: Input },],
'headingTpl': [{ type: Input },],
'sortable': [{ type: HostBinding, args: ['class.slds-is-sortable', ] },{ type: Input },],
'sortOrder': [{ type: Input },],
'ariaSort': [{ type: HostBinding, args: ['attr.aria-sort', ] },],
'onSort': [{ type: Output },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
